import React from "react";
import {
  Container,
  Icon,
  Segment,
  Form,
  Button,
  TextArea,
  Message,
  Header
} from "semantic-ui-react";
import firebase from "../config/firebase";

class AddNewPlace extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      category: "",
      city: "",
      documentName: "",
      description: "",
      linkReservation: "",
      name: "",
      openingHour: "",
      ticketPrice: "",
      photoLink: "",
      linkPhoto: [],
      longitude: "",
      latitude: "",
      region: "",
      error: false
    };
  }

  handleOnChange = e => {
    this.setState({
      [e.target.name]: e.target.value
    });
  };

  handleSubmit = e => {
    e.preventDefault();
    const coordinates = {
      longitude: this.state.longitude,
      latitude: this.state.latitude
    };
    const {
      category,
      city,
      description,
      linkPhoto,
      linkReservation,
      name,
      openingHour,
      ticketPrice,
      region
    } = this.state;
    const newData = {
      category,
      city,
      description,
      linkPhoto,
      linkReservation,
      name,
      openingHour,
      ticketPrice,
      coordinates,
      region
    };

    if (this.isFormValid()) {
      console.log(newData);
      firebase
        .firestore()
        .collection("Attraction")
        .doc(this.state.documentName)
        .set(newData);
      this.cleanFields();
    } else {
      this.setState({
        error: true
      });
    }
  };

  handleAddLink = () => {
    const newPhoto = [...this.state.linkPhoto, this.state.photoLink];
    this.setState({
      linkPhoto: newPhoto,
      photoLink: ""
    });
  };

  isFormValid = () => {
    return this.state.documentName;
  };
  cleanFields = () => {
    this.setState({
      category: "",
      city: "",
      documentName: "",
      description: "",
      linkReservation: "",
      ticketPrice: "",
      name: "",
      openingHour: "",
      photoLink: "",
      latitude: "",
      longitude: "",
      region: "",
      linkPhoto: [],
      error: false
    });
  };

  render() {
    const {
      category,
      city,
      documentName,
      description,
      linkReservation,
      ticketPrice,
      name,
      openingHour,
      photoLink,
      latitude,
      longitude,
      region,
      error
    } = this.state;
    return (
      <Container>
        <Segment basic>
          <Header as="h2" icon textAlign="center">
            <Icon name="location arrow" circular />
            <Header.Content>Polonia Tour</Header.Content>
          </Header>
          <Form style={{ maxWidth: "500px", margin: "auto" }}>
            <Form.Field>
              <Form.Input
                fluid
                placeholder="Document Name"
                icon="folder"
                iconPosition="left"
                name="documentName"
                value={documentName}
                error={error}
                onChange={this.handleOnChange}
              />
            </Form.Field>
            <Form.Field>
              <Form.Input
                fluid
                placeholder="Name"
                icon="font"
                iconPosition="left"
                name="name"
                value={name}
                onChange={this.handleOnChange}
              />
            </Form.Field>
            <Form.Field>
              <Form.Input
                fluid
                placeholder="Region"
                icon="location arrow"
                iconPosition="left"
                name="region"
                value={region}
                onChange={this.handleOnChange}
              />
            </Form.Field>
            <Form.Field>
              <Form.Input
                fluid
                placeholder="Category"
                name="category"
                icon="address card"
                iconPosition="left"
                value={category}
                onChange={this.handleOnChange}
              />
            </Form.Field>
            <Form.Field>
              <Form.Input
                fluid
                placeholder="City"
                icon="location arrow"
                iconPosition="left"
                name="city"
                value={city}
                onChange={this.handleOnChange}
              />
            </Form.Field>
            <Form.Group widths="equal">
              <Form.Input
                fluid
                placeholder="Longitude"
                icon="expand arrows alternate"
                iconPosition="left"
                name="longitude"
                value={longitude}
                onChange={this.handleOnChange}
              />
              <Form.Input
                fluid
                placeholder="Latitude"
                icon="expand arrows alternate"
                iconPosition="left"
                name="latitude"
                value={latitude}
                onChange={this.handleOnChange}
              />
            </Form.Group>
            <Form.Field>
              <Form.Field
                control={TextArea}
                style={{ resize: "none" }}
                onChange={this.handleOnChange}
                name="description"
                value={description}
                placeholder="Description"
              />
            </Form.Field>

            <Form.Field>
              <Form.Input
                fluid
                placeholder="Link Reservation"
                icon="linkify"
                iconPosition="left"
                name="linkReservation"
                value={linkReservation}
                onChange={this.handleOnChange}
              />
            </Form.Field>
            <Form.Field>
              <Form.Input
                fluid
                placeholder="Opening Hour"
                name="openingHour"
                icon="hourglass"
                iconPosition="left"
                value={openingHour}
                onChange={this.handleOnChange}
              />
            </Form.Field>
            <Form.Field>
              <Form.Input
                fluid
                placeholder="Ticket Price"
                icon="money"
                iconPosition="left"
                name="ticketPrice"
                value={ticketPrice}
                onChange={this.handleOnChange}
              />
            </Form.Field>
            <Form.Field>
              <Form.Input
                action
                fluid
                placeholder="Photo Links"
                name="photoLink"
                value={photoLink}
                onChange={this.handleOnChange}
              >
                <input />
                <Button type="submit" basic positive onClick={this.handleAddLink}>
                  <Icon name="plus" />
                  Add link
                </Button>
              </Form.Input>
            </Form.Field>
            {this.state.linkPhoto.length > 0 ? (
              <Form.Field>
                <Message style={{ wordWrap: "break-word" }}>
                  <Message.Header>Photo Links</Message.Header>
                  <Message.List key={1} items={this.state.linkPhoto} />
                </Message>
              </Form.Field>
            ) : null}
            <Button type="submit" onClick={this.handleSubmit}>
              <Icon name="save" />
              Add new place
            </Button>
          </Form>
        </Segment>
      </Container>
    );
  }
}

export default AddNewPlace;
